.middle{
  padding-top: 50%;
  padding-right: 30px;
  position: sticky;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
}

@media screen and (max-width: 480px) {
  .middle{
    padding-top: -40%;
  }
}
.btnCtn{
  display: inline-block;
  width: 90px;
  height: 90px;
  background: #f1f1f1;
  margin: 10px;
  border-radius: 30%;
  box-shadow: 0 5px 15px -5px #00000070;
  color: #f0b51f;
  overflow: hidden;
  position: relative;
}
.btnCtn i{
  line-height: 90px;
  font-size: 26px;
  transition: 0.2s linear;
}
.btnCtn:hover i{
  transform: scale(1.3);
  color: #f1f1f1;
}
.btnCtn::before{
  content: "";
  position: absolute;
  width: 120%;
  height: 120%;
  background: #f0b51f;
  transform: rotate(45deg);
  left: -110%;
  top: 90%;
}
.btnCtn:hover::before{
  animation: aaa 0.7s 1;
  top: -10%;
  left: -10%;
}
@keyframes aaa {
  0%{
    left: -110%;
    top: 90%;
  }50%{
    left: 10%;
    top: -30%;
  }100%{
    top: -10%;
    left: -10%;
  }
}