    .wrap-inner {
      width:700px;
      margin: 0 auto;
    }

      .info {
        display: block;
        position: absolute;
        z-index: 10;		    
        padding: 15px;
        width: 350px;
          color: black;
          font-size: 27px;
          font-family:  'Roboto', sans-serif;
          font-weight: 300;
          opacity: 0;
          transition: all .52s ease-in-out;
      }

      #b1 .info,
      #b3 .info,
      #b4 .info {
          top: 130px;
          left: 0px;		    

        transform: translate(150px,-150px) rotateX(-10deg) rotateY(44deg) rotateZ(7deg) scale(.95);
      }

      #b2 .info,
      #b5 .info {
          top: 130px;
          left: 0px;		    
        text-align: right;
        transform: translate(150px,-150px) rotateX(-10deg) rotateY(44deg) rotateZ(7deg) scale(.95);
      }

      #b1:hover > .info,
      #b3:hover > .info,
      #b4:hover > .info {
        transform: translate(150px,-150px) rotateX(-10deg) rotateY(44deg) rotateZ(7deg) scale(1);
        opacity: 1;
        left: 300px;
        top: 0;
      }

      #b2:hover > .info {
        transform: translate(150px,-150px) rotateX(-10deg) rotateY(44deg) rotateZ(7deg) scale(1);
        opacity: 1;
        top: 450px;
        left: -515px;
      }

      #b5:hover > .info {
          opacity: 1;
          top: 450px;
          left: -570px;
      }
